<template>
  <div class="header">
    <router-link to="/" class="header-logo"></router-link>
    <div class="select df-center" @click="openCity = !openCity">
      {{cityName(city)}}
      <span class="select-input" v-text="`${cityName(city)}｜${modeName}`"></span>
      <button :class="['select-btn', { down: !openCity }]">
        <i class="icofont-circled-up"></i>
      </button>
    </div>
  </div>
</template>

<script>
import { cityLib, modeLib } from '../lib.js'

export default {
  name: 'Header',
  props: { city: String, setCity: Function },
  data () {
    return {
      openCity: false,
      searchMode: 'ScenicSpot'
    }
  },
  computed: {
    modeName () {
      return modeLib[this.searchMode]
    }
  },
  methods: {
    cityName (city) {
      return cityLib[city].name
    }

  }
}
</script>

<style lang='scss'>
.header-logo {
  width: 150px;
  height: 70px;
  background: url(data:image/png;base64,)
    no-repeat 50% / contain;
}
.icofont-circled-up:before {
  content: "";
}
.select {
  position: relative;
  margin-top: 1.5rem;
  z-index: 2;
  cursor: pointer;
  .textbox {
    flex-wrap: nowrap;
    margin: 1rem 0;
    padding: 0.5rem 0.75rem;
    background-color: #fafafa;
    border: 1px solid #c8c8c8;
    border-radius: 0.5rem;
  }
}
.select-btn, .textbox-btn {
  font-size: 1.5rem;
  color: var(--c-main);
  background: transparent;
  text-decoration: none;
  border: none;
  outline: none;
  transform: rotate(1turn);
  transition: transform .5s;
  cursor: pointer;
  &.select-btn.down {
    transform: rotate(180deg);
  }
}
</style>



